<template>
  <div class="web" :class="{index:index}">
    <header>
      <div class="logo">
        <router-link to="/" tag="a"></router-link>
      </div>
      <nav>
        <ul>
          <router-link to="/articles" tag="li">文章</router-link>
          <router-link to="/works" tag="li">作品</router-link>
          <router-link to="/lifes" tag="li">生活</router-link>
          <router-link to="/motto" tag="li">格言</router-link>
          <router-link to="/about" tag="li">关于</router-link>
        </ul>
      </nav>
    </header>
    <router-view :expleft="expleft" :expup.sync="expup"></router-view>
    <ul class="rightNav">
      <li class="iconfont icon-menu pc" v-if="detail" @click="navleft"></li>
      <li class="iconfont icon-menu mobile" v-if="detail" @click="navup"></li>
      <li class="iconfont icon-l-goToTop" @click="totop"></li>
    </ul>

  </div>
</template>
<script>
import '@/assets/css/lee.css'
import '@/assets/css/lee-mb.css'

export default {
    name: 'myweb',
    data() {
        return {
            index: false,
            detail: false,//是不是详细页
            expleft: true,//详细页PC端那个导航关闭与否
            expup: false,//详细页移动端那个导航关闭与否
        }
    },
    mounted() {
        let sw = window.innerWidth
        let _this = this
        cl(sw)
        window.addEventListener('resize', () => {
            sw = window.innerWidth
            cl(sw)
        })

        function cl(sw) {
            if (sw > 768) {
                _this.expleft = true
                _this.expup = false
            } else {
                _this.expleft = false
                _this.expup = false
            }
        }

    },
    methods: {
        totop() {
            document.documentElement.scrollIntoView({behavior: "smooth", block: "start"});
        },
        navleft() {
            this.expleft = !this.expleft
        },
        navup() {
            this.expup = !this.expup
        }
    },
    computed: {},
    watch: {
        $route(to) {
            this.expup = false
            this.expleft = true
            if (to.fullPath === '/') {
                this.index = true
            } else {
                this.index = false
            }
            if (to.meta.detail) {
                this.detail = true
            } else {
                this.detail = false
            }
        }
    }
}
</script>
